<!--
 * @Description: 设备列表 - 下盖
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-08 10:08:44
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex v-if="level_type !== 2" xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="agent_id"
              :items="agent_list"
              label="所属代理商"
              required
            ></v-select>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="base_below_sn" label="底座SN号码"></v-text-field>
          </v-flex>
          <!-- <v-flex xs6 sm4 md2>
            <v-text-field v-model="base_above_sn" label="上盖SN号码"></v-text-field>
          </v-flex>-->
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="blue_tooth" label="蓝牙名称"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="status"
              :items="['全部设备', '已激活', '未使用']"
              label="设备状态"
              required
            ></v-select>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info">筛选设备</v-btn>
        </v-layout>
      </v-card-title>
      <!-- 
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template slot="headers" slot-scope="props">
          <tr>
            <th>
              <v-checkbox
                color="error"
                :input-value="props.all"
                :indeterminate="props.indeterminate"
                primary
                hide-details
                @click.native="toggleAll"
              ></v-checkbox>
            </th>
            <th
              v-for="header in props.headers"
              :key="header.text"
              :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
              @click="changeSort(header.value)"
            >
              <!-- <v-icon small>arrow_upward</v-icon> -->
              {{ header.text }}
            </th>
          </tr>
        </template>
        <template v-slot:items="props">
          <tr>
            <td :active="props.selected" @click="props.selected = !props.selected">
              <v-checkbox color="error" :input-value="props.selected" primary hide-details></v-checkbox>
            </td>
            <!-- <td class="text-md-center text-xs-centre" >{{ props.index+1 }}</td> -->
            <td class="text-xs-centre text-md-center">{{ props.item.base_below_sn }}</td>
            <td class="text-xs-centre text-md-center">{{ props.item.blue_tooth }}</td>
            <td class="text-xs-centre text-md-center">
              <span v-if="props.item.agent_name !== ''">{{ props.item.agent_name }}</span>
              <i v-else>- - -</i>
            </td>
            <!-- <td class="text-xs-centre">{{ props.item.base_above_sn }}</td> -->

            <td class="text-xs-centre text-md-center">{{ props.item.create_time | time }}</td>
            <td class="text-xs-centre text-md-center">{{ props.item.update_time | time }}</td>
            <td class="text-xs-centre text-md-center">
              <span class="green--text darken-4" v-if="props.item.status==1">使用中</span>
              <span v-if="props.item.status !== 1">未激活</span>
              <!-- {{ props.item.status==1?"使用中":"未激活" }} -->
            </td>
            <td class="text-xs-centre text-md-center">
              <span
                class="green--text darken-4"
                v-if="props.item.user_name !== ''"
              >{{ props.item.user_name }}</span>
              <i v-else>- - -</i>
            </td>
            <td class="text-xs-centre text-md-center">
              <v-menu
                bottom
                dark
                origin="center center"
                :close-delay="100"
                transition="slide-x-transition"
              >
                <template v-slot:activator="{ on }">
                  <v-btn color="primary" dark v-on="on">操作选项</v-btn>
                </template>
                <v-list>
                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/ed_record' && props.item.user_name !== ''"
                      
                      @click="ED_data( props.item.user_id )"
                    >
                      <v-list-tile-title>ED记录</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/ehs' && props.item.user_name !== ''"
                      
                      @click="EHS_data( props.item.user_id )"
                    >
                      <v-list-tile-title>EHS记录</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/iief5' && props.item.user_name !== ''"
                      
                      @click="iief5_data( props.item.user_id )"
                    >
                      <v-list-tile-title>IIEF5记录</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/cases_file' && props.item.user_name !== ''"
                      
                      @click="files_data( props.item.user_id )"
                    >
                      <v-list-tile-title>病例档案</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-if="props.item.user_name !== ''">
                    <v-list-tile  @click="device_top( props.item.user_id )">
                      <v-list-tile-title>关联上盖</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/device/delete_sn'"
                      
                      @click="device_( props.item.id )"
                    >
                      <v-list-tile-title class="red--text">删除设备</v-list-tile-title>
                    </v-list-tile>
                  </template>
                </v-list>
              </v-menu>

              <!-- <v-btn v-if="props.item.user_name !== ''" @click="ED_data( props.item.user_id )">ED记录</v-btn>
            <v-btn v-if="props.item.user_name !== ''" @click="EHS_data( props.item.user_id )">EHS记录</v-btn>
            <v-btn
              v-if="props.item.user_name !== ''"
              @click="iief5_data( props.item.user_id )"
            >iief5记录</v-btn>
              <v-btn v-if="props.item.user_name !== ''" @click="files_data( props.item.user_id )">病例档案</v-btn>-->
              <!-- <template v-for="(item,index) in root_path">
            <v-btn :key="index+150" v-if="item.url == '/api/v1/device/delete_sn' || props.item.user_name == ''" color="error" @click="device_delete( props.item.user_id )">删除设备</v-btn>
              </template>-->
              <!-- <i v-if="props.item.user_name == ''">设备未关联用户</i> -->
            </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
      <v-btn color="error" flat v-if="selected.length > 0" @click="device_list()">😫 批量删除</v-btn>
    </v-card>
    <!-- 删除确认框 -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="290">
        <v-card>
          <v-card-title class="headline">删除提醒</v-card-title>
          <v-card-text>确认要删除选中的设备吗？确认删除将不可恢复。请谨慎操作！</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog = false">取消</v-btn>
            <v-btn color="green darken-1" flat="flat" @click="device_delete()">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";

export default {
  components: {
    Breadcrumbs
  },
  data() {
    return {
      // 筛选状态
      agent_id: null,
      base_below_sn: null,
      base_above_sn: null,
      blue_tooth: null,
      status: null,

      agent_list: [],
      agent_list_data: [],
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      allList: 0,
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "底座列表",
          disabled: false,
          to: "/deviceList"
        }
      ],
      pagination: {
        sortBy: "name"
      },
      selected: [],
      headers: [
        // { text: "#", align: "left", sortable: false, value: false},
        { text: "设备底座sn", value: "fat", sortable: false },
        { text: "蓝牙", value: "protein", sortable: false },
        { text: "所属代理商", value: "calories", sortable: false },

        // { text: "设备上盖sn", value: "carbs", sortable: false },

        { text: "创建时间", value: "iron", sortable: false },
        { text: "更新时间", value: "iron", sortable: false },
        { text: "状态", value: "iron", sortable: false },
        { text: "用户账号", value: "iron", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path,
      level_type: this.$cookie.getLocalStorage("root_path").level,
      dialog: false,
      id: []
    };
  },
  mounted: function() {
    this.agent_data();
  },
  methods: {
    // 关联上盖
    device_top(user_id) {
      this.$router.push({
        path: `/deviceList_`,
        query: {
          user_id: user_id
        }
      });
    },
    // 全选
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else this.selected = this.desserts.slice();
    },
    // 单选
    changeSort(column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
    },
    // 删除一条
    device_(id) {
      this.dialog = true;
      this.id.push(id);
    },
    // 批量删除
    device_list() {
      this.id = [];
      this.dialog = true;
      this.selected.forEach(item => {
        this.id.push(item.id);
      });
    },
    // 删除操作
    device_delete() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.device_delete_sn,
        data: {
          ids: this.id,
          is_base: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.dialog = false;
            this.id = [];
            this.$Dialog.getProductCheckResult_({text: "已删除该设备！",timeout: 3000, snackbar: true });
            this.info();
          } else {
            this.$Dialog.getProductCheckResult_({text:  "😡" + response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
    // 代理列表
    agent_data() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_agent,
        data: {
          id: -1,
          level: 2
        }
      })
        .then(response => {
          this.agent_list.push("全部代理");
          this.agent_list_data = response.data.datas;
          if (response.data.result === 0) {
            this.agent_list_data.forEach(item => {
              this.agent_list.push(item.user_name);
            });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
    // ED 记录
    ED_data(id) {
      this.$router.push({
        path: `/ED`,
        query: {
          user_id: id
        }
      });
    },
    // EHS 记录
    EHS_data(id) {
      this.$router.push({
        path: `/EHS`,
        query: {
          user_id: id
        }
      });
    },
    // iief5记录
    iief5_data(id) {
      this.$router.push({
        path: `/iief5`,
        query: {
          user_id: id
        }
      });
    },
    // 病历档案
    files_data(id) {
      this.$router.push({
        path: `/files`,
        query: {
          user_id: id
        }
      });
    },
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    // 设备列表
    info(event) {
      this.loader = true;
      let agent_id_ = null;
      this.agent_list_data.forEach(item => {
        if (this.agent_id == item.user_name) {
          agent_id_ = item.id;
          return false;
        }
      });
      this.$axios({
        method: "post",
        url: this.$apis.URL.device_query_sn,
        data: {
          agent_id: agent_id_,
          base_below_sn: this.base_below_sn,
          base_above_sn: this.base_above_sn,
          blue_tooth: this.blue_tooth,
          status:
            this.status == "全部状态"
              ? null
              : this.status == "已激活"
              ? 1
              : this.status == "未使用"
              ? -1
              : null,
          page: this.page,
          page_count: (typeof(event)=="undefined"?undefined:event.rowsPerPage),
          type: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            if (this.$route.query.user_id !== undefined) {
              this.uID_info(this.$route.query.user_id);
            } else {
              this.desserts = response.data.datas.list;
              this.allList = response.data.datas.total_number;
            }
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // 带参筛选设备列表
    uID_info(user_id) {
      this.$axios({
        method: "post",
        url: this.$apis.URL.device_query_sn,
        data: {
          user_id: user_id - 0,
          type: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
</style>
